import React, { useState, useEffect } from "react";
import { AdminPage, Panel } from "@admin/components/admin";
import Table from "@admin/components/common/Table";
import SelectSize from "@admin/components/common/SelectSize";
import Pagination from "@admin/components/common/Pagination";
import { Select, Button, DatePicker, Tooltip, Modal } from "antd";
import deviceProvider from "@data-access/device-provider";
import DataContants from "@admin/components/config/data-contants";
import snackbar from "@utils/snackbar-utils";
import "./style.scss";
const { confirm } = Modal;
const { Option } = Select;
export default function index(props) {
  const [state, _setState] = useState({
    size: 10,
    page: 0,
    data: [],
  });
  const setState = (data = {}) => {
    _setState((state) => {
      return { ...state, ...data };
    });
  };
  const onSizeChange = (size) => {
    onSearch("size", size);
    setState({
      size: size
    })
  };
  const onPageChange = (page) => {
    onSearch("page", page - 1);
    setState({
      page: page
    })
  };
  useEffect(() => {
    onSearch(0, 10);
  }, []);
  const onSearch = (action, item) => {
    let page = action === "page" ? item : action === "size" ? 0 : state.page;
    let size = action === "size" ? item : state.size;
    let ma = action === "ma" ? item : state.ma;
    let ten = action === "ten" ? item : state.ten;
    let serial = action === "serial" ? item : state.serial;
    let model = action === "model" ? item : state.model;
    let dmHangSanXuat = action === "dmHangSanXuat" ? item : state.dmHangSanXuat;
    let namSanXuat = action === "namSanXuat" ? item : state.namSanXuat;
    let dmDonViTinh = action === "dmDonViTinh" ? item : state.dmDonViTinh;
    let namSuDung = action === "namSuDung" ? item : state.namSuDung;
    let trangThai = action === "trangThai" ? item : state.trangThai;
    // page = page ? page : state.page,
    // size = size ? size : state.size,
    // ma = ten ? ten : state.ma,
    // ten = ten ? ten : state.ten,
    // serial = serial ? serial : state.serial,
    // model = model ? model : state.model,
    // dmHangSanXuat = dmHangSanXuat ? dmHangSanXuat : state.dmHangSanXuat,
    // namSanXuat = namSanXuat ? namSanXuat : state.namSanXuat,
    // dmDonViTinh = dmDonViTinh ? dmDonViTinh : state.dmDonViTinh,
    // namSuDung = namSuDung ? namSuDung : state.namSuDung,
    // trangThai = trangThai ? trangThai : state.trangThai) => {
    // setState({
    //     page: page,
    //     size: size,
    //     ma: ma,
    //     ten: ten,
    //     serial: serial,
    //     model: model,
    //     dmHangSanXuat: dmHangSanXuat,
    //     namSanXuat: namSanXuat,
    //     dmDonViTinh: dmDonViTinh,
    //     namSuDung: namSuDung,
    //     trangThai: trangThai
    // });

    deviceProvider
      .search(
        page,
        size,
        ma,
        ten,
        serial,
        model,
        dmHangSanXuat,
        namSanXuat,
        dmDonViTinh,
        namSuDung,
        trangThai
      )
      .then((s) => {
        if (s.code == 0) {
          setState({
            total: s.totalElements,
            data:
              s.data && s.data.length
                ? s.data.map((item, index) => {
                  return {
                    key: index,
                    col1: page * size + index + 1,
                    // col2: item,
                    col3: item.ma,
                    col4: item.ten,
                    col5: item.serial,
                    col6: item.model,
                    col7: item.dmHangSanXuat,
                    col8: item.namSanXuat,
                    col9: item.dmDonViTinh,
                    col10: item.namSuDung,
                    col11: item.trangThai,
                    col12: item,
                  };
                })
                : [],
          });
        }
      })
      .catch((e) => { });
  };

  const exportExcel = (item) => () => { };

  const viewDetail = (item) => {
    props.history.push(`/admin/device/` + item.id);
  };
  const editItem = (item) => {
    props.history.push(`/admin/device/edit/` + item.id);
  };
  const checkStatus = (item) => {
    let status = DataContants.listStatus.filter((item) => {
      return parseInt(item.id) === Number(item);
    });
    if (status.length > 0) return status[0];
    return {};
  };
  const onDeleteItem = (item) => {
    return new Promise((resolve, reject) => {
      confirm({
        title: "Xác nhận",
        content: `Bạn có muốn xóa thiết bị ${item.ten}?`,
        okText: "Xóa",
        okType: "danger",
        cancelText: "Hủy",
        onOk() {
          deviceProvider
            .delete(item.id)
            .then((s) => {
              if (s && s.code === 0) {
                snackbar.show("Xóa thành công", "success");
                onSearch(0, 10);
              } else {
                snackbar.show("Xóa không thành công", "danger");
              }
            })
            .catch((e) => { });
        },
        onCancel() {
          reject();
        },
      });
    });
  };
  const addNew = () => {
    props.history.push("/admin/device/create");
  };
  return (
    <AdminPage
      icon="subheader-icon fal fa-window"
      header="Quản lý trang thiết bị"
      subheader="Danh sách trang thiết bị"
    >
      <Panel
        id="panel-list-site"
        title="DANH SÁCH TRANG THIẾT BỊ"
        toolbar={
          <div className="toolbar">
            <Button className="button">Xuất Excel</Button>
            <Button className="button" onClick={addNew}>
              Thêm mới
            </Button>
          </div>
        }
      >
        <Table
          scroll={{ x: 800, y: 500 }}
          style={{ marginLeft: -10, marginRight: -10 }}
          className="custom"
          columns={[
            {
              title: (
                <div className="custome-header">
                  <div className="title-box">STT</div>
                  <div className="addition-box"></div>
                </div>
              ),
              width: 111,
              dataIndex: "col1",
              key: "col1",
            },
            // {
            //     title: (
            //         <div className="custome-header">
            //             <div className="title-box">Chọn</div>
            //             <div className="addition-box"></div>
            //         </div>
            //     ),
            //     width: 111,
            //     dataIndex: "col2",
            //     key: "col2",
            //     render: (item) => {
            //         return (
            //             <div class="custom-control custom-checkbox">
            //                 <input type="checkbox" onClick={() => onClickCheckbox(item)} class="custom-control-input" id="defaultChecked" checked={item.checked} />
            //                 <label class="custom-control-label" for="defaultChecked"></label>
            //             </div>
            //         );
            //     },
            // },
            {
              title: (
                <div className="custome-header">
                  <div className="title-box">Mã thiết bị</div>
                  <div className="addition-box">
                    <div className="search-box">
                      <img src={require("@images/icon/ic-search.png")} />
                      <input
                        value={state.ma}
                        onChange={(e) => onSearch("ma", e.target.value)}
                        placeholder="Tìm theo mã thiết bị"
                      />
                    </div>
                  </div>
                </div>
              ),
              width: 172,
              dataIndex: "col3",
              key: "col3",
            },
            {
              title: (
                <div className="custome-header">
                  <div className="title-box">Tên thiết bị</div>
                  <div className="addition-box">
                    <div className="search-box">
                      <img src={require("@images/icon/ic-search.png")} />
                      <input
                        value={state.ten}
                        onChange={(e) => onSearch("ten", e.target.value)}
                        placeholder="Tìm theo tên thiết bị"
                      />
                    </div>
                  </div>
                </div>
              ),
              width: 172,
              dataIndex: "col4",
              key: "col4",
            },
            {
              title: (
                <div className="custome-header">
                  <div className="title-box">Số máy</div>
                  <div className="addition-box">
                    <div className="search-box">
                      <img src={require("@images/icon/ic-search.png")} />
                      <input
                        value={state.serial}
                        onChange={(e) => onSearch("serial", e.target.value)}
                        placeholder="Tìm theo số máy"
                      />
                    </div>
                  </div>
                </div>
              ),
              width: 172,
              dataIndex: "col5",
              key: "col5",
            },
            {
              title: (
                <div className="custome-header">
                  <div className="title-box">Chủng loại</div>
                  <div className="addition-box">
                    <div className="search-box">
                      <img src={require("@images/icon/ic-search.png")} />
                      <input
                        value={state.model}
                        onChange={(e) => onSearch("model", e.target.value)}
                        placeholder="Tìm theo chủng loại"
                      />
                    </div>
                  </div>
                </div>
              ),
              width: 172,
              dataIndex: "col6",
              key: "col6",
            },
            {
              title: (
                <div className="custome-header">
                  <div className="title-box">Hãng sản xuất</div>
                  <div className="addition-box">
                    <div className="search-box">
                      <img src={require("@images/icon/ic-search.png")} />
                      <input
                        value={state.dmHangSanXuat}
                        onChange={(e) =>
                          onSearch("dmHangSanXuat", e.target.value)
                        }
                        placeholder="Tìm theo hãng sản xuất"
                      />
                    </div>
                  </div>
                </div>
              ),
              width: 172,
              dataIndex: "col7",
              key: "col7",
            },
            {
              title: (
                <div className="custome-header">
                  <div className="title-box">Năm sản xuất</div>
                  <div className="addition-box">
                    <div className="search-box">
                      <img src={require("@images/icon/ic-search.png")} />
                      <input
                        value={state.namSanXuat}
                        onChange={(e) => onSearch("namSanXuat", e.target.value)}
                        placeholder="Tìm theo năm sản xuất"
                      />
                    </div>
                  </div>
                </div>
              ),
              width: 172,
              dataIndex: "col8",
              key: "col8",
            },
            {
              title: (
                <div className="custome-header">
                  <div className="title-box">Đơn vị sử dụng</div>
                  <div className="addition-box">
                    <div className="search-box">
                      <img src={require("@images/icon/ic-search.png")} />
                      <input
                        value={state.dmDonViTinh}
                        onChange={(e) =>
                          onSearch("dmDonViTinh", e.target.value)
                        }
                        placeholder="Tìm theo đơn vị sử dụng"
                      />
                    </div>
                  </div>
                </div>
              ),
              width: 172,
              dataIndex: "col9",
              key: "col9",
            },
            {
              title: (
                <div className="custome-header">
                  <div className="title-box">Năm sử dụng</div>
                  <div className="addition-box">
                    <div className="search-box">
                      <img src={require("@images/icon/ic-search.png")} />
                      <input
                        value={state.namSuDung}
                        onChange={(e) => onSearch("namSuDung", e.target.value)}
                        placeholder="Tìm theo năm sử dụng"
                      />
                    </div>
                  </div>
                </div>
              ),
              width: 122,
              dataIndex: "col10",
              key: "col10",
            },
            {
              title: (
                <div className="custome-header">
                  <div className="title-box">Trạng thái</div>
                  <div className="addition-box">
                    <Select
                      value={state.trangThai}
                      onChange={(e) => {
                        onSearch("trangThai", e);
                      }}
                    >
                      {DataContants.listStatus &&
                        DataContants.listStatus.length &&
                        DataContants.listStatus.map((option, index) => {
                          return (
                            <Option key={index} value={option.id}>
                              {option.name}
                            </Option>
                          );
                        })}
                    </Select>
                  </div>
                </div>
              ),
              width: 150,
              dataIndex: "col11",
              key: "col11",
              render: (item) => {
                return (
                  <span>
                    {checkStatus(item) && checkStatus(item).name
                      ? checkStatus(item).name
                      : null}
                  </span>
                );
              },
            },
            {
              title: (
                <div className="custome-header">
                  <div className="title-box">Tiện ích</div>
                  <div className="addition-box"></div>
                </div>
              ),
              width: 150,
              dataIndex: "col12",
              key: "col12",
              fixed: "right",
              render: (item) => {
                return (
                  <div className="col-action">
                    <Tooltip placement="topLeft" title={"Xem chi tiết"}>
                      <div>
                        <a
                          onClick={() => viewDetail(item)}
                          className="btn btn-info btn-icon waves-effect waves-themed"
                        >
                          <i className="fal fa-eye"></i>
                        </a>
                      </div>
                    </Tooltip>
                    <Tooltip placement="topLeft" title={"Sửa"}>
                      <div>
                        <a
                          onClick={() => editItem(item)}
                          className="btn btn-info btn-icon waves-effect waves-themed"
                        >
                          <i className="fal fa-edit"></i>
                        </a>
                      </div>
                    </Tooltip>
                    <Tooltip placement="topLeft" title={"Xóa"}>
                      <div>
                        <a
                          onClick={() => onDeleteItem(item)}
                          className="btn btn-info btn-icon waves-effect waves-themed"
                        >
                          <i className="fal fa-trash-alt"></i>
                        </a>
                      </div>
                    </Tooltip>
                  </div>
                );
              },
            },
          ]}
          dataSource={state.data}
        ></Table>
        <div className="footer">
          <SelectSize value={state.size} selectItem={onSizeChange} />
          <Pagination
            onPageChange={onPageChange}
            page={state.page}
            size={state.size}
            total={state.total}
            style={{ flex: 1, justifyContent: "flex-end" }}
          />
        </div>
      </Panel>
    </AdminPage>
  );
}
